
'use client';

import { useEffect, useState } from 'react';
import { Bar, BarChart, CartesianGrid, XAxis, YAxis, ResponsiveContainer } from 'recharts';
import type { Building } from '@/types';
import { getActiveWorks } from '@/app/actions'; // Reusing getActiveWorks
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { ChartContainer, ChartTooltip, ChartTooltipContent, type ChartConfig } from '@/components/ui/chart';
import { Alert, AlertDescription, AlertTitle } from '@/components/ui/alert';
import { Loader2, BarChart3, Info } from 'lucide-react';

const chartConfig = {
  votes: {
    label: '票数',
    color: 'hsl(var(--primary))',
  },
} satisfies ChartConfig;

export default function VoteAnalysisPage() {
  const [analysisData, setAnalysisData] = useState<Building[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    async function fetchData() {
      setLoading(true);
      setError(null);
      try {
        const works = await getActiveWorks(); // Fetch active works sorted by votes
        setAnalysisData(works);
      } catch (err) {
        console.error("Error fetching vote analysis data:", err);
        setError("无法加载投票数据，请稍后再试。");
      } finally {
        setLoading(false);
      }
    }
    fetchData();
  }, []);

  if (loading) {
    return (
      <div className="flex flex-1 justify-center items-center h-full p-4">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-3 text-lg text-muted-foreground">正在加载分析数据...</p>
      </div>
    );
  }

  if (error) {
    return (
      <Alert variant="destructive" className="my-6">
        <AlertTitle>数据加载错误</AlertTitle>
        <AlertDescription>{error}</AlertDescription>
      </Alert>
    );
  }

  return (
    <div className="space-y-6">
      <Card className="shadow-lg">
        <CardHeader>
          <CardTitle className="text-2xl flex items-center">
            <BarChart3 className="mr-3 h-7 w-7 text-primary" />
            作品投票动态分析
          </CardTitle>
          <CardDescription>查看当前热门作品的得票情况（仅显示进行中活动的有效作品）。</CardDescription>
        </CardHeader>
        <CardContent>
          {analysisData.length > 0 ? (
            <ChartContainer config={chartConfig} className="min-h-[300px] w-full aspect-video">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart accessibilityLayer data={analysisData} margin={{ top: 5, right: 20, left: 0, bottom: 60 }}>
                  <CartesianGrid vertical={false} strokeDasharray="3 3" />
                  <XAxis
                    dataKey="name"
                    tickLine={false}
                    tickMargin={10}
                    axisLine={false}
                    angle={-35}
                    textAnchor="end"
                    height={80} // Adjust height to accommodate angled labels
                    interval={0} // Ensure all labels are shown
                    tick={{ fontSize: 12 }}
                  />
                  <YAxis 
                    tickLine={false}
                    axisLine={false}
                    tickMargin={5}
                    allowDecimals={false}
                  />
                  <ChartTooltip
                    cursor={false}
                    content={<ChartTooltipContent hideLabel />}
                  />
                  <Bar dataKey="votes" fill="var(--color-votes)" radius={5} />
                </BarChart>
              </ResponsiveContainer>
            </ChartContainer>
          ) : (
            <Alert className="border-primary/30 bg-primary/5 text-center py-10">
              <Info className="h-12 w-12 text-primary mx-auto mb-4" />
              <AlertTitle className="text-xl font-semibold text-primary/90 mb-2">
                暂无数据
              </AlertTitle>
              <AlertDescription className="text-primary/80">
                当前没有足够的进行中活动作品数据以供分析。
              </AlertDescription>
            </Alert>
          )}
        </CardContent>
      </Card>
       <Alert>
        <AlertTitle>数据说明</AlertTitle>
        <AlertDescription>
          <ul className="list-disc pl-5 space-y-1 text-sm">
            <li>图表展示了当前所有“进行中”活动里，状态为“有效”的建筑作品及其得票数。</li>
            <li>数据会随投票实时更新（刷新页面以查看最新数据）。</li>
            <li>作品名称较长时，在X轴上可能会倾斜或截断显示。</li>
          </ul>
        </AlertDescription>
      </Alert>
    </div>
  );
}
